@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

.expo {
    border-radius: 5px;
    opacity: .8;
    display: grid;
    grid-template-columns: 2fr 2fr;
    font-family: "Josefin Sans", sans-serif;
    background: rgb(24, 81, 149);
    background: linear-gradient(90deg,
            rgba(24, 81, 149, 1) 0%,
            rgba(15, 33, 54, 1) 100%);
    margin: 20px 0 0 0;
    box-shadow: black 3px 0 15px 1px;
    color: aliceblue;
    font-size: 17px;
    backdrop-filter: blur(5px);
}

.wrapper {
    align-items: flex-start;
    margin: 20px 0 20px 10px;
    display: flex;
    flex-direction: column;
}

.skill_item::before {
    content: "âŠ™ ";
}

.skill_item {
    text-shadow: rgb(0, 0, 0) 0 1px 25px;
}

.js,
.node {
    margin-left: 35px;
}

