@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

.header_nav {
    box-shadow: black 3px 0 15px 1px;
    border-radius: 0 0 5px 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    height: auto;
    opacity: 0.8;
    background: rgb(24, 81, 149);
    background: linear-gradient(90deg, rgba(24, 81, 149, 1) 0%, rgba(15, 33, 54, 1) 100%);
}

.logo_nav {
    text-shadow: rgb(0, 0, 0) 0 2px 5px;
    position: relative;
    font-size: 20px;
    color: aquamarine;
    margin-top: 9px;
    font-family: "Josefin Sans", sans-serif;
    width: 16ch;
    animation: typing 2s steps(22), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
}

.link_nav {
    font-family: "Josefin Sans", sans-serif;
    text-shadow: rgb(0, 0, 0) 0 2px 5px;
}

@keyframes typing {
    from {
        width: 0
    }
}

@keyframes blink {
    50% {
        border-color: transparent
    }
}

