.photo {
    text-align: center;
    border-radius: 20% 5px 20px;
    max-width: 100%;
    margin-top: 15px;
    margin-left: 20px;
    margin-bottom: 15px;
    box-shadow:
        0px 0px 0.9px rgba(0, 0, 0, 0.115),
        0px 0px 3.2px rgba(0, 0, 0, 0.116),
        0px 0px 8.6px rgba(0, 0, 0, 0.139),
        0px 0px 19.3px rgba(0, 0, 0, 0.243),
        0px 0px 36px rgba(0, 0, 0, 0.62);
}

